import React, { useState, useEffect } from 'react';
import { walletService } from '../../services/walletService';
import WalletCard from './WalletCard';
import WalletModal from './WalletModal';
import CopyableAddress from '../common/CopyableAddress';
import './WalletManager.css';

const WalletManager = () => {
  const [wallets, setWallets] = useState([]);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [modalType, setModalType] = useState('add');
  const [selectedWallet, setSelectedWallet] = useState(null);

  // 
  const fetchWallets = async () => {
    try {
      setLoading(true);
      const walletList = await walletService.getWallets(true, 'solana');
      setWallets(walletList);
    } catch (error) {
      setMessage(`: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleImportWallet = async (walletData) => {
    try {
      setLoading(true);
      await walletService.importWallet(walletData);
      setMessage('');
      setShowModal(false);
      setTimeout(() => fetchWallets(), 1000);
    } catch (error) {
      setMessage(`: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleEditWallet = async (walletData) => {
    try {
      setLoading(true);
      await walletService.editWallet(walletData);
      setMessage('');
      setShowModal(false);
      setTimeout(() => fetchWallets(), 1000);
    } catch (error) {
      setMessage(`: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleDeleteWallet = async (walletId) => {
    try {
      setLoading(true);
      await walletService.deleteWallet(walletId);
      setMessage('');
      setShowModal(false);
      setTimeout(() => fetchWallets(), 500);
    } catch (error) {
      setMessage(`: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const openModal = (type, wallet = null) => {
    setModalType(type);
    setSelectedWallet(wallet);
    setShowModal(true);
    setMessage('');
  };

  // 
  useEffect(() => {
    fetchWallets();
  }, []);

  return (
    <div className="wallet-management">
      <div className="section-header">
        <h2> </h2>
        <div className="header-actions">
          <button 
            className="btn btn-primary"
            onClick={() => openModal('add')}
            disabled={loading}
          >
             
          </button>
          <button 
            className="btn btn-outline"
            onClick={fetchWallets}
            disabled={loading}
          >
             
          </button>
        </div>
      </div>

      {message && (
        <div className={`message ${message.includes('') ? 'success' : 'error'}`}>
          {message}
        </div>
      )}
      
      {loading && (
        <div className="loading">
          <div className="spinner"></div>
          <span>...</span>
        </div>
      )}
      
      <div className="wallet-grid">
        {wallets.length === 0 ? (
          <div className="empty-state">
            <div className="empty-icon"></div>
            <h3></h3>
            <p>""</p>
          </div>
        ) : (
          wallets.map(wallet => (
            <WalletCard
              key={wallet.id || wallet._id}
              wallet={wallet}
              onEdit={() => openModal('edit', wallet)}
              onDelete={() => openModal('delete', wallet)}
              onInfo={() => openModal('info', wallet)}
            />
          ))
        )}
      </div>

      {showModal && (
        <WalletModal
          type={modalType}
          wallet={selectedWallet}
          onClose={() => setShowModal(false)}
          onImport={handleImportWallet}
          onEdit={handleEditWallet}
          onDelete={handleDeleteWallet}
          loading={loading}
        />
      )}
    </div>
  );
};

export default WalletManager; 