import React, { useState, useEffect } from 'react';

const WalletModal = ({ type, wallet, onClose, onImport, onEdit, onDelete, loading }) => {
  const [formData, setFormData] = useState({
    name: '',
    privateKey: '',
    type: 'solana'
  });

  // 
  useEffect(() => {
    if (type === 'edit' && wallet) {
      setFormData({
        name: wallet.name || '',
        privateKey: '',
        type: wallet.type || 'solana'
      });
    } else if (type === 'add') {
      setFormData({
        name: '',
        privateKey: '',
        type: 'solana'
      });
    }
  }, [type, wallet]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (type === 'add') {
      onImport(formData);
    } else if (type === 'edit') {
      onEdit({
        _id: wallet.id || wallet._id,
        name: formData.name
      });
    } else if (type === 'delete') {
      onDelete(wallet.id || wallet._id);
    }
  };

  const renderAddForm = () => (
    <form onSubmit={handleSubmit}>
      <div className="form-group">
        <label></label>
        <input
          type="text"
          name="name"
          value={formData.name}
          onChange={handleInputChange}
          placeholder=""
          required
        />
      </div>
      <div className="form-group">
        <label></label>
        <input
          type="password"
          name="privateKey"
          value={formData.privateKey}
          onChange={handleInputChange}
          placeholder=""
          required
        />
      </div>
      <div className="form-group">
        <label></label>
        <select
          name="type"
          value={formData.type}
          onChange={handleInputChange}
        >
          <option value="solana">Solana</option>
          <option value="evm">EVM</option>
          <option value="tron">TRON</option>
        </select>
      </div>
      <div className="modal-actions">
        <button type="button" className="btn btn-outline" onClick={onClose}>
          
        </button>
        <button type="submit" className="btn btn-primary" disabled={loading}>
          {loading ? '...' : ''}
        </button>
      </div>
    </form>
  );

  const renderEditForm = () => (
    <form onSubmit={handleSubmit}>
      <div className="form-group">
        <label></label>
        <input
          type="text"
          name="name"
          value={formData.name}
          onChange={handleInputChange}
          placeholder=""
          required
        />
      </div>
      <div className="form-group">
        <label></label>
        <input
          type="text"
          value={formData.type}
          disabled
          className="form-control-disabled"
        />
        <small className="form-help"></small>
      </div>
      <div className="modal-actions">
        <button type="button" className="btn btn-outline" onClick={onClose}>
          
        </button>
        <button type="submit" className="btn btn-primary" disabled={loading}>
          {loading ? '...' : ''}
        </button>
      </div>
    </form>
  );

  const renderDeleteConfirm = () => (
    <div className="delete-confirmation">
      <div className="warning-icon"></div>
      <h3></h3>
      <p> "{wallet?.name}" </p>
      <p className="warning-text"></p>
      <div className="modal-actions">
        <button className="btn btn-outline" onClick={onClose}>
          
        </button>
        <button 
          className="btn btn-danger" 
          onClick={handleSubmit}
          disabled={loading}
        >
          {loading ? '...' : ''}
        </button>
      </div>
    </div>
  );

  const renderWalletInfo = () => (
    <div className="wallet-details">
      <h3></h3>
      <div className="detail-row">
        <span className="label">:</span>
        <span className="value">{wallet?.name}</span>
      </div>
      <div className="detail-row">
        <span className="label">ID:</span>
        <span className="value">{wallet?.id || wallet?._id}</span>
      </div>
      <div className="detail-row">
        <span className="label">:</span>
        <span className="value">{wallet?.address}</span>
      </div>
      <div className="detail-row">
        <span className="label">:</span>
        <span className="value">{wallet?.type}</span>
      </div>
      <div className="detail-row">
        <span className="label">:</span>
        <span className="value">{wallet?.sort}</span>
      </div>
      <div className="modal-actions">
        <button className="btn btn-primary" onClick={onClose}>
          
        </button>
      </div>
    </div>
  );

  const getModalTitle = () => {
    switch (type) {
      case 'add': return '';
      case 'edit': return '';
      case 'delete': return '';
      case 'info': return '';
      default: return '';
    }
  };

  const renderContent = () => {
    switch (type) {
      case 'add': return renderAddForm();
      case 'edit': return renderEditForm();
      case 'delete': return renderDeleteConfirm();
      case 'info': return renderWalletInfo();
      default: return null;
    }
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={e => e.stopPropagation()}>
        <div className="modal-header">
          <h2>{getModalTitle()}</h2>
          <button className="modal-close" onClick={onClose}></button>
        </div>
        <div className="modal-body">
          {renderContent()}
        </div>
      </div>
    </div>
  );
};

export default WalletModal; 